.app-wrapper {
    display: grid;
    width: 100vw;
    height: 100vh;
    margin: 0 auto;
    grid-template-areas:
    "h h"
    "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /*grid-gap: 10px;*/
}
.app-wrapper-content {
    grid-area: c;
    background: #78cce1;
}

